import BootstrapTheme from "@fullcalendar/bootstrap"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import MetaTags from 'react-meta-tags'
import "./cal.css"
import DetailModal from "./DetailModal"
import url from "../../common/api"
import  { Redirect } from 'react-router-dom'
import {
    Card,
    Col,
    Container,
    Row,
    CardBody,
    CardHeader,
    CardTitle,
    FormGroup,
    Label,
    Button,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Breadcrumb,
    BreadcrumbItem,
   
    
  
  } from "reactstrap"

const events = [{
    id: 1,
    color: '#fd3153',
    from: '2021-05-02T18:00:00+00:00',
    to: '2021-05-05T19:00:00+00:00',
    title: 'ceci est un evenement'
}, {
    id: 2,
    color: '#1ccb9e',
    from: '2019-05-01T13:00:00+00:00',
    to: '2019-05-05T14:00:00+00:00',
    title: 'This is another event'
}, {
    id: 3,
    color: '#3694DF',
    from: '2019-05-05T13:00:00+00:00',
    to: '2019-05-05T20:00:00+00:00',
    title: 'This is also another event'
}];

class liste extends React.Component {
    constructor(props) {
        super(props)
        this.handleDateClick = this.handleDateClick.bind(this)
        this.calendarComponentRef = React.createRef()
    
        this.state = {
          calendarWeekends: true,
          modal: false,
          modalcategory: false,
          isDragBind: false,
          detailModal: false,
          events: [],
          event :{},
          ok:"",
        }
    
        this.toggle = this.toggle.bind(this)
       
      }
      componentDidMount() {
        let date='23/25/2014'
        let conv=date.split('/');
        console.log("date"+conv[2]+"-"+conv[1]+"-"+conv[0])
        this.handleChargerData()
      }

      toggle() {
        this.setState(prevState => ({
          modal: !prevState.modal,
        }))
      }
      handleDateClick = arg => {
        this.setState({ selectedDay: arg })
        this.toggle()
      }  
      handleEventClick = arg => {
        const event = arg.event
        this.setState({detailModal:true})
        console.log("evenement:",event)
        this.setState({
            event:event._def.extendedProps
        //   event: {
        //     id: event.id,
        //     title: event.title,
        //     title_category: event.title_category,
        //     start: event.start,
        //     className: event.classNames,
        //     category: event.classNames[0],
        //     event_category: event.classNames[0],
        //   },
         
        })
        this.toggle()
      }
      handleChargerData=()=>{
        var data=JSON.parse(localStorage.getItem("authAshia")).profile
        //this.setState({events:[{title:"titre1" ,start:"2021-06-02",end:"2021-06-05"}]})
       fetch(url+"profiles/"+data.id)
          .then((response) => response.json())
          .then((data) => {
            console.log("les evenements,",data)
            data.evenements.map((element,idx) =>{
             let conv=element.date_debut.split('/')
console.log("la dayte", element.date_debut.split('/')[2]+"-"+element.date_debut.split('/')[1]+"-"+element.date_debut.split('/')[0])
              var tab= {
                title: element.libelle,
                //thematique: element.nom,
                id_event: element.id,
                //format: element.format.code,
                //langue: element.nom,
                color: "red",
                libelle :element.libelle,
                lieu: element.lieu,
                heure_debut: element.heure_debut,
                heure_fin: element.heure_fin,
                description: element.description,
                start: element.date_debut.split('/')[2]+"-"+element.date_debut.split('/')[1]+"-"+element.date_debut.split('/')[0],
                end: element.date_fin.split('/')[2]+"-"+element.date_fin.split('/')[1]+"-"+element.date_fin.split('/')[0],
                //typesystemeirrigue: element.nom,
             
        
                
              }
             
              this.setState({events:[...this.state.events,tab]})
            })
          
            
            
           }
           
         );
      }
      handleAjouter=()=>{
        this.setState({ok: <Redirect to='/ajout-evenement'  />});
      }
    render() {
        const { isEdit, detailModal } = this.state
        return (
            <React.Fragment>    {this.state.ok}
             <DetailModal
                show={detailModal}
                event={this.state.event}
                onCloseClick={() => this.setState({ detailModal: false })}
            />
                 <MetaTags>
                    <title>Calendrier | SIS</title>
                </MetaTags>
                <Container fluid={true} id="cal">
                <Breadcrumbs title="SIS" breadcrumbItem="Calendrier" />

                <Row className="justify-content-start mt-2 mb-2">
                <Col md="6" className="pull-left">
                  <Button
                    color="info"
                    className="font-16 btn-block btn btn-info  float-left"

                    onClick={this.handleAjouter}

                  >
                    <i className="mdi mdi-plus-circle-outline me-1" />
                    Crer un nouvel venement
                  </Button>
                </Col>
                <Col md="6" ></Col>
                </Row>

                <Row>
                <Col className="col-lg-12">
                 <FullCalendar
                    plugins={[
                            BootstrapTheme,
                            dayGridPlugin,
                            interactionPlugin,
                          ]}
                          
                          slotDuration={"00:15:00"}
                          handleWindowResize={true}
                          themeSystem="bootstrap"
                         // events={events}
                          editable={true}
                          droppable={true}
                          selectable={true}
                          dateClick={this.handleDateClick}
                          eventClick={this.handleEventClick}
                         // drop={this.onDrop}
                         events={this.state.events}
            />
            </Col>
            </Row>
             </Container>
            </React.Fragment>
           
        );
    }
}

export default liste