import React from "react"
import PropTypes from 'prop-types'
import { Col, Modal, ModalBody, Row } from "reactstrap"
import { format } from 'date-fns';

const DetailModal = ({ show, onCloseClick ,event}) => {
  return (
    <Modal isOpen={show} toggle={onCloseClick} centered={true}>
      <ModalBody className="py-3 px-5">
        <Row>
          <Col lg={12}>
            <div className="text-center">
              <i
                className="mdi mdi-alert-circle-outline"
                style={{ fontSize: "6em", color: "orange" }}
              />
          
            </div>
          </Col>
        </Row>
        <Row>
          <Col lg={12}>
            <div className="text-center">
             <div>libelle: <span>{event.libelle}</span></div>
             <div>lieu: <span>{event.lieu}</span></div>
             <div>debute a: <span>{event.heure_debut}</span></div>
             <div>finit a : <span>{event.heure_fin}</span></div>
             <div>description:<p>{event.description}</p></div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            <div className="text-center mt-3">
              
              <button
                type="button"
                className="btn btn-danger btn-lg me-2"
                onClick={onCloseClick}
              >
                Fermer
              </button>
            </div>
          </Col>
        </Row>
      </ModalBody>
    </Modal>
  )
}

DetailModal.propTypes = {
  onCloseClick: PropTypes.func,
  event: PropTypes.any,
  show: PropTypes.any
}

export default DetailModal
